/* harmony default export */ var good_detail = ({
  data: function data() {
    return {
      title: '',
      flag: true,
      datas: {},
      showCar: false,
      showBuy: false,
      addNum: 0,
      //1. 2.
      from: 1
    };
  },

  computed: {
    backCash: function backCash() {
      return this.datas.goods_return_rate * this.datas.goods_price;
    }
  },
  components: {
    HeaderBack: src_components_header_back,
    AddBuy: pages_good_detail_buy_add,
    GoodDetailSwitch: pages_good_detail_good_detail_switch,
    ProductDetail: pages_good_detail_product_detail_list,
    DetailList: pages_good_detail_detal_goodlist,
    EditNum: src_components_edit_num
  },
  watch: {
    showCar: function showCar(val, oldVal) {
      if (val) {
        document.getElementsByTagName("body")[0].className = "active_pop";
      } else {
        document.body.removeAttribute("class", "active_pop");
      }
    }
  },
  methods: {

    //
    ensure_add: function ensure_add() {
      var carNum = this.$store.state.addCarNum;
      if (carNum > 0) {
        //
        if (this.from === 1) {
          this.addShopSucess(this.$route.params.id, carNum);
        } else {
          //
          this.instantBuy(this.$route.params.id, this.$store.state.addCarNum);
        }
      } else {
        //=0
        utils.ToastUtil("");
      }
    },


    //
    instantBuy: function instantBuy(goodId, num) {

      console.log("====goodId===" + goodId + "====num====" + num);
      var token = localStorage.getItem('token');
      //               var data = qs.stringify({
      //                 cart_id: goodId+"|"+num,
      //                 ifcart: '0'
      //               });
      //               this.$http.post('gp/mobile/index.php?' + 'act=member_buy&op=buy_step1&token='+token+"&method=post", data,{
      //                 headers: {
      //                   'Content-Type': 'application/x-www-form-urlencoded'
      //                 }
      //               })
      //                 .then(function (response) {
      //                   console.log(response);
      //                 })
      //                 .catch(function (response) {
      //                   console.log(response);
      //                 });

      //vue-resource
      this.$http.post('gp/mobile/index.php?' + 'act=member_buy&op=buy_step1&token=' + token + "&method=post", {
        'cart_id': goodId + "|" + num,
        'ifcart': '0'
      }, { emulateJSON: true }).then(function (res) {
        // 
        console.log(res.body);
      }, function (err) {
        // 
        console.log(err);
      });
    },


    //
    addShopSucess: function addShopSucess(goodId, carNum) {
      var token = localStorage.getItem('token');
      var that = this;
      //               this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=member_cart&op=cart_add&token='+token,
      this.$http.jsonp('gp/mobile/index.php?act=member_cart&op=cart_add&token=' + token, {
        params: {
          goods_id: goodId,
          quantity: carNum
        },
        jsonp: 'callback',
        jsonpcallback: 'addtoCart'
      }).then(function (res) {
        var jsonStr = JSON.parse(res.bodyText);

        if (jsonStr.code === 0) {
          utils.ToastUtil(jsonStr.msg);
          that.showCar = false;
        }
      }, function () {
        Toast("");
      });
    },
    closeAddDialog: function closeAddDialog() {
      this.showCar = false;
    },
    refresh: function refresh(goodId) {
      this.initDetail(goodId);
    },

    //banner
    handleChange: function handleChange(index) {
      console.log('item   ' + index);
    },
    back: function back() {
      this.$router.go(-1);
    },

    //
    addshopCar: function addshopCar() {

      this.showCar = !this.showCar;
      this.from = 1;
    },

    //
    buy: function buy() {

      this.showCar = !this.showCar;
      console.log("");
      this.from = 2;
    },
    switchIndex: function switchIndex(index) {
      if (index === 1) {
        //
        this.flag = true;
      } else {
        //
        this.flag = false;
      }
    },
    initDetail: function initDetail(goodId) {
      var token = localStorage.getItem('token');
      var that = this;
      this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=goods&op=goodsDetail', {
        params: {
          goods_id: goodId,
          token: token
        },
        jsonp: 'callback',
        jsonpcallback: 'detail'
      }).then(function (res) {
        that.datas = JSON.parse(res.bodyText).datas;
      }, function () {
        Toast("");
      });
    }
  },
  created: function created() {
    this.initDetail(this.$route.params.id);
    console.log("======goodId====" + this.$route.params.id);
  }
});
