import { Toast } from 'mint-ui';
const util={
  //
     vaildPhone:function (phone) {
       if(phone.length==0){
         this.ToastUtil('');
         return false;
       }
       let myreg =  /^1\d{10}$/ ;
       if(phone.length!=11) {
         this.ToastUtil('')
         return false;
       }
       if(!myreg.test(phone)){
         this.ToastUtil('')
         return false;
       }
         return true
     },
  ToastUtil(msg){
    Toast({
      message: msg,
      position: 'bottom',
      duration: 3000
    });
  }
}
export default util;



