<!---->
<template lang="html">
  <div class="regist_container">
    <header-view :titles="title" @back="back"></header-view>
    <div class="phone">
      <span class="phone_icon"></span>
      <div class="phone_input">
        <input type="text" style="display: none;"/>

        <input type="number" placeholder="" v-model.trim="mobile" debounce="500" autocomplete="new-password">
      </div>
    </div>
    <div class="password">
      <span class="pwd_icon"></span>
      <div class="password_input">
        <input type="password" style="display: none;"/>

        <input type="password" placeholder="" v-model.trim="pwd"  autocomplete="new-password">
      </div>
    </div>

    <div class="verfycode">
      <span class="verfycode_icon"></span>
      <div class="verfycode_input">
        <input type="text" style="display: none;"/>
        <input type="number" placeholder="" v-model.trim="identify_code"  autocomplete="new-password">
      </div>
      <div class="line"></div>
      <div class="send_verfy_code" @click="sendCode">
        <span></span>
      </div>
    </div>
    <div class="regist_btn" @click="regist"></div>
  </div>
</template>
<script>
  import HeaderView from '../../components/header_back'
  import Utils from '../../common/utils'
  export default {
      data(){
        return{
         title:"",
          mobile:'',
          pwd:'',
          identify_code:'',
          hasRegist:false //
      }
      },
    watch:{
        //
      mobile:{
        handler:function (val ,oldval) {
          if(val.length===11){
            this.checkMobile();
          }
        }
      },
      deep:true
    },
      components:{
        HeaderView
      },
    methods:{
        back(){
          this.$router.go(-1);
        },
      //
      checkMobile(){
        if(!Utils.vaildPhone(this.mobile)){
          return;
          }
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=login&op=checkMobile", //
          {
            params: {
              username:this.mobile
            },
            jsonp:'callback',
            jsonpcallback	:'checkUserName',
          }).then(function(res){
          const response=JSON.parse(res.bodyText);
          if(response.code===5){
            Utils.ToastUtil(response.msg);
            this.hasRegist=true
          }else{
            this.hasRegist=false
          }
        },function(){
          Utils.ToastUtil("")
        });
      },
      //
      sendCode(){
        if(this.hasRegist){
          Utils.ToastUtil("");
          return;
        }
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=sms&op=index", //
          {
            params: {
              mobile:this.mobile
            },
            jsonp:'callback',
            jsonpcallback	:'verifyCode',
          }).then(function(res){
          const response=JSON.parse(res.bodyText);
          Utils.ToastUtil(response.msg)
        },function(){
          Utils.ToastUtil("")
        });

      },
      //
      regist(){
        if(this.hasRegist){
          Utils.ToastUtil("");
          return;
        }
        if(!Utils.vaildPhone(this.mobile)){
          return;
        }
        if(this.pwd.length===0){
          Utils.ToastUtil("");
          return;
        }
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=login&op=register", //
          {
            params: {
              member_name : this.mobile,
              member_password : this.pwd,
              invite_code :"" ,
              sms_code : this.identify_code
            },
            jsonp:'callback',
            jsonpcallback	:'register',
          }).then(function(res){
          const response=JSON.parse(res.bodyText);
          localStorage.setItem('token',response.datas.token.token)
        },function(){
          Utils.ToastUtil("")
        });

      }
    }

  }
</script>
<style scoped lang="css">
@import "../../css/reset.css";
  @import "regist.css";
</style>



